$colors: (
  "bg-black": #2f303a,
  "blue": #2196f3,
  "black": #212121,
  "grey": #757575,
  "white": #ffffff,
  "line": #ececec,
  "our-team-bg": #f5f4fa,
  "white-bg-btn": #eeeeee,
  "contacts-footer": rgba(255, 255, 255, 0.6),
  "inactive-bg-btn": #f5f4fa,
  "hero-bg": rgba(47, 48, 58, 0.4),
  "social-media-icon": #afb1b8,
  "customers": #afb1b8,
  "sm-icon": rgba(255, 255, 255, 0.1),
);
@function getColor($key) {
  @return map-get($colors, $key);
}
@function transitionFunc($key) {
  @return $key 250ms cubic-bezier(0.4, 0, 0.2, 1);
}
